.gpt4__navbar{
    display:flex ;
    justify-content: space-between  ;
    align-items: center ;
    padding: 2rem 6rem ;
}

.gpt4__navbar-links{
    flex: 1;
    display: flex ;
    justify-content: flex-start;
    align-items: center;
}

.gpt4__navbar-links_logo{
    margin-right: 2rem;
}

.gpt4__navbar-links_logo img{
    width: 82.56px;
    height: 36.02px;
}

.gpt4__navbar-links_container{
    display: flex;
    flex-direction: row;
}

.gpt4__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt4__navbar-links_container p,
.gpt4__navbar-sign p,
.gpt4__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

