button {
    background: transparent;
    color: #fff;
    font-size: 17px;
    font-family: var(--font-family);
    font-weight: 600;
    border: none;
    padding: 20px 30px;
    perspective: 30rem;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.308);
    cursor: pointer;
  }
  
  button::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 10px;
    background: linear-gradient(320deg, rgba(0, 140, 255, 0.678), rgba(128, 0, 128, 0.308));
    z-index: 1;
    transition: background 3s;
  }
  
  button:hover::before {
    animation: rotate 1s;
    transition: all .5s;
  }
  
  @keyframes rotate {
    0% {
      transform: rotateY(180deg);
    }
  
    100% {
      transform: rotateY(360deg);
    }
  }

  .gpt4__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
  }

  .gpt4__navbar-menu svg{
    cursor: pointer;
  }

  .gpt4__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top:40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2)
  }

  .gpt4__navbar-menu_container p{
    margin: 1rem;
  }

  .gpt4__navbar-menu_container-links-sign{
    display:none;
  }

  @media screen and (max-width: 1050px) {
   .gpt4__navbar-links_container{
      display: none;
    }
    .gpt4__navbar-menu{
        display: flex;
    }
  }

  @media screen and (max-width: 700px) {
    .gpt4__navbar{
        padding: 2rem 4rem;
    }
   }

